form {
    width: 80%;
    height: 60%;
    max-width: 500px;
    max-height: 480px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-color: #484848;
    border-radius: 1.5rem;
    box-shadow: 0 0.1rem 0.2rem black;
}

label {
    font-size: 2.3rem;
    font-weight: 700;

    margin-bottom: 1rem;

    text-shadow: 0 0.1rem 0.2rem black;
}

textarea {
    width: 70%;
    height: 35%;

    border: none;
    overflow: auto;
    outline: none;
    resize: none;

    padding: 0.5rem;
    margin-bottom: 1.5rem;

    text-align: center;
    vertical-align: center;
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;

    border-radius: 1.5rem;
    box-shadow: 0 0.1rem 0.2rem;
}

textarea:focus {
    outline: white double;
}

textarea::placeholder {
    text-align: center;
    vertical-align: center;
    font-size: 1.2rem;
}

.TodoForm-buttonContainer {
    width: 70%;

    display: flex;
    justify-content: space-around;
}

.TodoForm-button {
    height: 3.2rem;

    border: none;
    padding: 0 0.8rem;
    border-radius: 1.5rem;
    box-shadow: 0 0.1rem 0.2rem;

    font-family: 'Poppins', sans-serif;
    font-size: 1.2rem;

    cursor: pointer;
}

.TodoForm-button--cancel {
    background-color: #EA4C46;
}

.TodoForm-button--cancel:hover {
    outline: #EA4C46 double;
}

.TodoForm-button--submit {
    background-color: #567c7c;
}

.TodoForm-button--submit:hover {
    outline: #567c7c double;
}

@media (min-width: 500px) {
    textarea {
        font-size: 1.5rem;
    }

    textarea::placeholder {
        font-size: 1.5rem;
    }

    .TodoForm-button {
        font-size: 1.35rem;
    }
}